import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import Icon28Play from '@vkontakte/icons/dist/28/play';
import Icon28MoneySendOutline from '@vkontakte/icons/dist/28/money_send_outline';
import Icon28MoneyRequestOutline from '@vkontakte/icons/dist/28/money_request_outline';
import {View, Panel, PanelHeaderSimple, Card, Avatar, Cell, Title, Div, CardGrid, PopoutWrapper, Headline, Header, Spinner, FormLayoutGroup, Group, Counter, ScreenSpinner} from '@vkontakte/vkui';

import plastic from './img/plastic.png'
import metal from './img/metal.png';
import organic from './img/bin.png';
import glass from './img/glass.png';
import paper from './img/paper.png';
import other from './img/other.png';
import '@vkontakte/vkui/dist/vkui.css';
import './vivify.min.css';

import PopoutError from './panels/PopoutError';

import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import axios from 'axios';

const api = new VKMiniAppAPI();

const url = "https://vkgamewasteapp.000webhostapp.com/";

const colors = [
	{color:'#fff'},
	{color:'#ccc'},
	{color:'#bbf'},
	{color:null},
	{color:null},
	{color:null}
]

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
		   activePanel:'game',
		   activeView:'game',
		   activeModal:null,
		   item_className:"",
		   disabled:true,
		   user:null,
           item:null
		}
	}
	 
	componentDidMount() {
      axios.post(url, JSON.stringify({type:'get_random_card', url:window.location.href})).then(res => {
		  this.setState({item:res.data});
		  console.log(res);
		  
	  })


	  setInterval(() => {
		  axios.post(url, JSON.stringify({type:'tick', url:window.location.href})).then(res=>{
			  this.setState({user:res.data})
		
			  
		  })
	  }, 1000)
	}


	openImage = (img) => {
      api.showImages([img]);
	}

	put = (e) => {
	  //
	 // this.setState({item_className:"vivify ball duration-1000"});
	  //setTimeout(() => {
		  if(this.state.disabled) {
			  this.setState({disabled:false, popout:<ScreenSpinner />})
		  let trash = e.currentTarget.dataset.trash;
		  axios.post(url, JSON.stringify({type:'check_post', trash:trash, waste_id:this.state.item.waste_id, url:window.location.href}))
		  .then((response) => {

			  console.log(response);
			  if(response.data.status == 'success') {
				this.setState({item_className:"vivify popOutBottom duration-550", popout:null})
				setTimeout(() => {this.setState({item:response.data.item, disabled:true})}, 400);
				setTimeout(() => {this.setState({item_className:"vivify popInLeft"})}, 550)
			  }else {
				this.setState({item_className:"vivify popOutTop duration-550", popout:null});
				setTimeout(() => {this.setState({popout:<PopoutError name={this.state.item.name} description={response.data.description} src={this.state.item.img} kg={response.data.kg} onClose={()=>this.setState({popout:null,disabled:true, item_className:"vivify popInLeft"})} share={()=>this.share(response.data)} />, item:response.data.item})}, 550);
			  }
		  })
		  //this.setState({item_className:"vivify popOutBottom duration-550"})
		  //setTimeout(() =>{this.setState({item_className:"vivify popInLeft"})}, 550)
		//}, 1000)
	  //
	  }
	}


	share = (response) => {
		const canvas = document.createElement("canvas");
		canvas.width = 1080;
		canvas.height = 1920;
	    const ctx = canvas.getContext("2d");
		let maxWidth = 900; //размер поле, где выводится текст
		let lineHeight = 70;

		let marginLeft = 100;
    	let marginTop = 50;

		ctx.font = "70pt Calibri";
		ctx.fillStyle = "#000";
		ctx.align = "center";
		this.wrapText(ctx, response.description, marginLeft, marginTop, maxWidth, lineHeight);
		let o = canvas.toDataURL();
		console.log(o);
		
		api.showStoryBox({background_type:'image',url:'https://klike.net/uploads/posts/2019-11/1572778685_5.jpg', stickers:[{
			sticker_type:'renderable',
			sticker:{
				content_type:'image',
				url:url+this.state.item.img,
				transform:{
					gravity:'center_top'
				}
			}
		}, 
	    {
		   sticker_type:'renderable',
		   sticker:{
			   content_type:'image',
			   blob:o,
			   transform:{
				   gravity:'center'
			   }
		   }
		}]});
	}

   wrapText(context, text, marginLeft, marginTop, maxWidth, lineHeight) {
        var words = text.split(" ");
        var countWords = words.length;
        var line = "";
        for (var n = 0; n < countWords; n++) {
            var testLine = line + words[n] + " ";
            var testWidth = context.measureText(testLine).width;
            if (testWidth > maxWidth) {
                context.fillText(line, marginLeft, marginTop);
                line = words[n] + " ";
                marginTop += lineHeight;
            }
            else {
                line = testLine;
            }
        }
        context.fillText(line, marginLeft, marginTop);
    }



	render() {
		const { activePanel, user, activeView, activeModal, item, popout } = this.state;
		return (
	
			<View id='game' activePanel='game' header={false} popout={popout}>
			<Panel id='game'>
                <PanelHeaderSimple>Игра</PanelHeaderSimple>
		<Group header={<Header mode='secondary' aside={<Counter mode='primary'>{user == null ? <Spinner size='small' /> : user.k+" кг/c"}</Counter>}>Счет</Header>} align='center' style={{marginBottom:10}}>
				  {user == null ? <Spinner size='medium' /> :
                  <Title level={1} weight="heavy">{user.mass+" кг"}</Title>
				  }
				 </Group>
				<div align='center' style={{marginBottom:10, marginLeft:30, marginRight:30}}>
				<CardGrid style={{margin:'0 auto', display:'block'}}>	
				{item == null ? <Spinner size='large'/>:
                <Card size="l" mode="outline" className={this.state.item_className} id='item'>
				  <div>	
					<Avatar style={{top:10}} src={url+item.img} mode="image" size={64} onClick={()=>this.openImage(url+item.img)}/>
					<Title weight='semibold' level={2} style={{padding:15}}>{item.name}</Title>
				  </div>	
				</Card>
            	}
				</CardGrid>
				</div>

				<CardGrid>
					<Card size='s' onClick={this.put} data-trash="plastic" style={{backgroundColor:'#F19356'}}>
                      <Div align='center'>
						  <Avatar src={plastic} style={{top:10}}/>
                          <p>Пластик</p>
					  </Div>
					</Card>

					<Card size='s' style={{backgroundColor:'#D1CB73'}}>
					<Div align='center' onClick={this.put} data-trash="metal" >
						  <Avatar src={metal} style={{top:10}}/>
                          <p>Металл</p>
					</Div>
					</Card>

					<Card size='s' onClick={this.put} data-trash="organic" style={{backgroundColor:'#68B1E5'}}>
				<Div align='center'>
						  <Avatar src={organic} style={{top:10}}/>
                          <p>Органика</p>
					</Div>
					</Card>
				</CardGrid>

				<CardGrid>


					<Card size='s' onClick={this.put} data-trash="glass" style={{backgroundColor:'#68E5C4'}}>
					<Div align='center'>
						  <Avatar src={glass} style={{top:10}}/>
                          <p>Стекло</p>
					</Div>
					</Card>

					<Card size='s' onClick={this.put} data-trash="paper" style={{backgroundColor:'#BEF264'}}>
					<Div align='center'>
						  <Avatar src={paper} style={{top:10}}/>
                          <p>Бумага</p>
					</Div>
					</Card>

					<Card size='s' onClick={this.put} data-trash="other" style={{backgroundColor:'#84887D'}}>
					<Div align='center'>
						  <Avatar src={other} style={{top:10}}/>
                          <p>Другое</p>
					</Div>
					</Card>
				</CardGrid>


            </Panel>
			</View>
		
		)
	}
}

export default App;

